  padding: 0;
  width: 22px;
  height: 41px;
  border: 0;
  font-style: inherit;
  background-color: transparent;
  cursor: pointer;
}
.arrows__arrow-button::before {
  content: "";
  position: absolute;
  top: -44px;
  width: 27px;
  height: 27px;
  border-bottom: none;
  border-left: none;
  border-top: 2px solid rgba(40, 54, 69, 0.3);
  border-right: 2px solid rgba(40, 54, 69, 0.3);
}
