.form-registration__label-checkbox {
  font-size: 18px;
  position: relative;
  display: block;
  padding-left: 57px;
  padding-top: 5px;
  padding-bottom: 4px;
  color: #283645;
  cursor: pointer;
}
.form-registration__label-checkbox::before {
  content: "";
  position: absolute;
  left: 0;
  top: 50%;
  transform: translateY(-50%);
  width: 39px;
  height: 39px;
  border: 2px solid #E5E5E5;
  border-radius: 2px;
  background-color: #ffffff;
}
.form-registration__label-checkbox::after {
  content: "";
  display: none;
  position: absolute;
  left: 8px;
  top: 11px;
  transform: rotate(-45deg);
  width: 21px;
  height: 10px;
  border-left: 2px solid #D22856;
  border-bottom: 2px solid #D22856;
}
.form-registration__checkbox:checked + .form-registration__label-checkbox::after {
  display: block;
}
.form-registration__label-radio {
  font-size: 18px;
  color: #283645;
  position: relative;
  display: block;
